@font-face {
  font-family: 'Nunito Sans Bold';
  src: url('/fonts/NunitoSans-Bold.eot');
  src: url('/fonts/NunitoSans-Bold.eot?#iefix') format('embedded-opentype'),
    url('/fonts/NunitoSans-Bold.woff2') format('woff2'), url('/fonts/NunitoSans-Bold.woff') format('woff'),
    url('/fonts/NunitoSans-Bold.ttf') format('truetype'), url('/fonts/NunitoSans-Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Nunito Sans Regular';
  src: url('/fonts/NunitoSans-Regular.eot');
  src: url('/fonts/NunitoSans-Regular.eot?#iefix') format('embedded-opentype'),
    url('/fonts/NunitoSans-Regular.woff2') format('woff2'), url('/fonts/NunitoSans-Regular.woff') format('woff'),
    url('/fonts/NunitoSans-Regular.ttf') format('truetype'), url('/fonts/NunitoSans-Regular.svg') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
